  --main-color: #84a178;
  --rose-color: #d6936d;
  --accent-color: #f8d24e;
  --primary-white-color: #fcfcfc;
  --footer-copyright-color: #7a7a7a;
  --composition-main-color: #9cbe8f;
  --mobile-bg: #85a279;
  --tablet-bg: #97af8d;
  --modal-bg: #fcfcfc;
  --border-main-color: #e1e1e1;
  --milkshakes-bg-color: #d6936d;
  --ice-cream-bg-color: #aec6a5;
  --ice-coffe-bg-color: #efd478;
